/* src/components/SessionPage.css */
.session-page-container {
    padding: 20px;
  }
  
  .session-details {
    margin-bottom: 20px;
  }
  
  .session-details a {
    color: #4A90E2;
    text-decoration: none;
    font-weight: bold;
  }
  
  .session-details a:hover {
    text-decoration: underline;
  }
  
  .qr-code {
    margin-top: 10px;
  }
  
  .prayer-content {
    overflow-y: scroll;
    max-height: 60vh;
    padding: 10px;
    border: 1px solid #ccc;
  }
  
  .highlight {
    background-color: #ffff99;
    transition: background-color 0.5s ease;
  }
  
  .controls {
    margin-top: 20px;
    display: flex;
    justify-content: center;
    gap: 20px;
  }
  
  .controls button {
    padding: 10px 20px;
    background-color: #4A90E2;
    color: white;
    border: none;
    cursor: pointer;
    font-size: 16px;
  }
  
  .controls button:disabled {
    background-color: #ccc;
    cursor: not-allowed;
  }
  
  .controls button:hover:not(:disabled) {
    background-color: #357ABD;
  }
  